const ShareGenerator = (evaluated) => {
  const sqr = { 0: "", 1: "", 2: "", 3: "", 4: "" };
  let toBeShared = `     `;
  let exists = false;
  if (evaluated) {
    evaluated.forEach((ev) => {
      if (ev.length) {
        exists = true;
        ev.forEach((e) => {
          toBeShared += sqr[e];
        });
        toBeShared += "\n     ";
      }
    });
  }
  if(!exists){
    return ' ' + window.location.href
  }
  return " " + localStorage.col + " " + new Date().toDateString()+ "\n\n" + toBeShared
};

export default ShareGenerator
