import React from "react";
import "./Info.css";
import Modal from "react-modal";

Modal.setAppElement("#root");
function Info({ infoStatus, setInfoStatus }) {
  return (
    <div className="modalWrapper">
      <Modal
        isOpen={infoStatus}
        onRequestClose={() => setInfoStatus(false)}
        className="info-modal"
        overlayClassName="Overlay"
      >
        <div className="modalHeader">
          <button
            className="modalBtn"
            onClick={() => {
              setInfoStatus(false);
            }}
          >
             &#215;
          </button>
          <h3 className="headerText"><u> </u></h3>
        </div>
        <div className="modalBody">
          <div className="section">
                 <br></br>
            <br></br>
                /       
            <br></br>
            <br></br>
                        
            
            <br></br>
          </div>
          <div className="section">
            <h4> - : </h4>
            <div className="grid_wrapper for-info">
              <div className="grid_row row-0 final ">
                <div className="row_items correct a0 "></div>
                <div className="row_items a1 "></div>
                <div className="row_items a2 "></div>
                <div className="row_items a3 "></div>
              </div>
            </div>
            <p>   ()      </p>
            <div className="grid_wrapper for-info">
              <div className="grid_row row-0 final ">
                <div className="row_items  a0 "></div>
                <div className="row_items correctWrongPlace a1"></div>
                <div className="row_items a2 "></div>
                <div className="row_items a3 "></div>
              </div>
            </div>
            <p>    ()       </p>
            <div className="grid_wrapper for-info">
              <div className="grid_row row-0 final ">
                <div className="row_items correctFamily a0 "></div>
                <div className="row_items  a1"></div>
                <div className="row_items a2 "></div>
                <div className="row_items a3 "></div>
              </div>
            </div>
            <p>
                         (<u></u>  {" "}
              <u></u>   )
            </p>
            <div className="grid_wrapper for-info">
              <div className="grid_row row-0 final ">
                <div className="row_items a0 "></div>
                <div className="row_items a1"></div>
                <div className="row_items correctFamilyWrongPlace a2 "></div>
                <div className="row_items a3 "></div>
              </div>
            </div>
            <p>
                         (<u></u> 
               <u></u>   )
            </p>
            <div className="grid_wrapper for-info">
              <div className="grid_row row-0 final ">
                <div className="row_items a0 "></div>
                <div className="row_items a1"></div>
                <div className="row_items a2 "></div>
                <div className="row_items incorrect a3 "></div>
              </div>
            </div>
            <p>
                   (<u></u>      ) ()    
              !
            </p>
          </div>
          <div className="section">
            <b>   !</b>
          </div>
        </div>
      </Modal>
    </div>
  );
}

export default Info;
